body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #18182D!important;
  color: #18182D!important;
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

p {
  margin-top: 2px;
  margin-bottom: 10px;
}

i{
  height: 90px;
}

hr{
  width:70%;
  border: 1px solid #18182D!important;
}

input{
  border-radius: 20px;
  padding-left: 6px;
  padding-right: 6px;
  margin-bottom: 20px!important;
}

input:focus{
  outline: 0!important;
}

h1 {
  color:#ffffff;
}

.container{
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #1b1b1b;
  height: 100vh;
}

.office {
  display: flex;
  flex-direction: column;
  margin-top: 30px;
  width: 100%;
  justify-content: space-around;
}
.form-add-city {
  margin-bottom: 6px!important;
}
.office-box {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}
.close-button {
  background-color: #ffffff;
  border: 2px solid #ebebeb;
  position: relative;
  left: 100px;
  bottom: 34px;
  font-weight: bold;
  focus:none;
}

.close-button:hover {
  background-color: #a71d31;
}
  
button {
  background-color: #6DC0B5;
  transition: background-color 0.5s ease 0s;
}
button:hover{
  background-color: #467b74;
  color: #ffffff;
}

button:focus {
  outline: 0!important;
}

.tile { 
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border-radius: 6px;
  background-color: #ffffff;
  width: 14%;
  min-width: 200px;
  padding:20px 6px 40px;
  margin-bottom: 30px;
}

.temperature{
  font-size: 1.6em;
}